import {
  HashRouter as Router,
  Routes,
  Route,
} from "react-router-dom";
import HomePage from "./pages/HomePage"; // HomePage 

function App() {
  return (
    <Router basename="/iloke/frontend">
      <Routes>
        {/* /  HomePage  */}
        <Route path="/" element={<HomePage />} />
        <Route path="/*" element={<HomePage />} />
      </Routes>
    </Router>
  );
}

export default App;
