import React, { useState, useEffect } from "react";
import { selectBoardAnswer } from "../../services/api"; // API  
import PaginationControls from "../pagination/PaginationControls";
import BoardInsert from "./BoardInsert";

import "./BoardRegistrationPage.css";

const BoardRegistrationPage = () => {
  const [boards, setBoards] = useState([]); //  
  const [selectedCode, setSelectedCode] = useState("nt"); //  
  // const [listCode, setListCode] = useState(null); //  
  const [currentPage, setCurrentPage] = useState(1); //  
  const [selectedId, setSelectedId] = useState(null); //  ID
  const [selectedTitle, setSelectedTitle] = useState(null); //  ID
  const [selectedContents, setSelectedContents] = useState(null); //  
  const [boardInsert, setBoardInsert] = useState(false); //    

  const itemsPerPage = 10; //    
  const totalPages = Math.ceil(boards.length / itemsPerPage); //   

  //   
  useEffect(() => {
    const end = new Date();
    const start = new Date();
    start.setDate(start.getDate() - 7);

    const startDate = start.toISOString().split("T")[0]; // yyyy-mm-dd  
    const endDate = end.toISOString().split("T")[0]; // yyyy-mm-dd  
    document.getElementById("start-date").value = startDate;
    document.getElementById("end-date").value = endDate;

    searchBoards();
  }, []); // `selectedCode`    

  const searchBoards = (event) => {
    if (event) {
      event.preventDefault();
    }

    // list_code = selectedCode;
    // setListCode(selectedCode);

    const frmData = new FormData(document.myFrm);
    frmData.append("code", selectedCode);
    const myData = Object.fromEntries(frmData);

    selectBoardAnswer(myData)
      .then((res) => {
        // if (JSON.stringify(res.data) !== JSON.stringify(boards)) {
        setBoards(res.data);
        setCurrentPage(1); //       
        // }
      })
      .catch((error) => {
        console.error("Error fetching boards:", error);
      });
  };

  //   
  const handlePageChange = (page) => {
    if (page >= 1 && page <= totalPages) {
      setCurrentPage(page);
    }
  };

  //   
  const handleDateButtonClick = (days, event) => {
    if (event) event.preventDefault();

    const today = new Date();

    if (days === 7 || days === 15) {
      today.setDate(today.getDate() - days);
    } else if (days === 30) {
      today.setMonth(today.getMonth() - 1);
    } else if (days === 90) {
      today.setMonth(today.getMonth() - 3);
    } else if (days === 365) {
      today.setFullYear(today.getFullYear() - 1);
    }

    document.getElementById("start-date").value = today
      .toISOString()
      .split("T")[0];
  };

  //    
  const indexOfLastItem = currentPage * itemsPerPage;
  const indexOfFirstItem = indexOfLastItem - itemsPerPage;
  const currentItems = boards.slice(indexOfFirstItem, indexOfLastItem);

  const handleRowClick = (id, title, contents, code) => {
    setSelectedId(id);
    setSelectedTitle(title);
    setSelectedContents(contents); //    
    setSelectedCode(code); //    
    setBoardInsert(true); //   
  };

  return (
    <div className="inquiry-section">
      <div className="filter-header">
        <h1>  </h1>
      </div>
      <form name="myFrm">
        <div className="mypage-date-check">
          <div className="mypage-date-period">
            <span className="mypage-period-text"></span>
            <button data-value="0" onClick={(e) => handleDateButtonClick(0, e)}>
              
            </button>
            <button data-value="7" onClick={(e) => handleDateButtonClick(7, e)}>
              7
            </button>
            <button
              data-value="15"
              onClick={(e) => handleDateButtonClick(15, e)}
            >
              15
            </button>
            <button
              data-value="30"
              onClick={(e) => handleDateButtonClick(30, e)}
            >
              1
            </button>
            <button
              data-value="90"
              onClick={(e) => handleDateButtonClick(90, e)}
            >
              3
            </button>
            <button
              data-value="365"
              onClick={(e) => handleDateButtonClick(365, e)}
            >
              1
            </button>
          </div>
          <div className="mypage-date-box">
            <input
              id="start-date"
              name="start_date"
              type="date"
              className="mypage-first-date"
            />
            &nbsp;-&nbsp;
            <input
              id="end-date"
              name="end_date"
              type="date"
              className="mypage-last-date"
            />
          </div>
          <div>
            <span className="mypage-period-text"></span>
            <select
              name="code"
              className="color-option"
              value={selectedCode}
              onChange={(e) => setSelectedCode(e.target.value)}
            >
              <option value="nt"></option>
              <option value="fq">FAQ</option>
            </select>
          </div>
          <div>
            <span className="mypage-period-text"></span>
            <input type="text" name="title" />
          </div>
          <div>
            <span className="mypage-period-text"></span>
            <input type="text" name="reg_id" />
          </div>
          <div className="mypage-date-search">
            <button className="mypage-date-search-btn" onClick={searchBoards}>
              
            </button>
          </div>
        </div>
      </form>

      <div className="table_container">
        <table id="boardTable">
          <thead>
            <tr>
              <th style={{ width: "10%" }}></th>
              <th style={{ width: "15%" }}></th>
              <th style={{ width: "8%" }}></th>
              <th style={{ width: "10%" }}></th>
              <th style={{ width: "20%" }}></th>
              <th style={{ width: "10%" }}></th>
              <th style={{ width: "20%" }}></th>
              <th style={{ width: "7%" }}></th>
            </tr>
          </thead>
          <tbody id="boardBody">
            {currentItems.map((item) => (
              <tr
                key={item.id}
                onClick={() =>
                  handleRowClick(item.id, item.title, item.contents, item.code)
                }
              >
                <td>{item.id}</td>
                <td>{item.title}</td>
                <td>{item.read_no}</td>
                <td>{item.reg_id}</td>
                <td>{item.reg_date}</td>
                <td>{item.upt_id}</td>
                <td>{item.upt_date}</td>
                <td
                  style={{ color: item.delete_yn === "" ? "blue" : "red" }}
                >
                  {item.delete_yn}
                </td>
              </tr>
            ))}
          </tbody>
        </table>

        {boardInsert && (
          <>
            <div
              className="modal-backdrop"
              onClick={() => setBoardInsert(false)}
            ></div>
            <div className="detail-pane">
              <BoardInsert
                id={selectedId}
                title={selectedTitle}
                contents={selectedContents} //  prop 
                code={selectedCode}
                onClose={() => setBoardInsert(false)}
                onSearch={searchBoards} // searchBoards  
              />
            </div>
          </>
        )}
        <div>
          <button
            type="button"
            className="write-btn"
            onClick={() => handleRowClick(null, null, null, selectedCode)}
          >
            
          </button>
        </div>
        <PaginationControls
          currentPage={currentPage}
          totalPages={totalPages}
          onPageChange={handlePageChange}
        />
      </div>
    </div>
  );
};

export default BoardRegistrationPage;
