import { useState, useEffect } from "react";
//import { Link } from 'react-router-dom';
import MemberDetail from "./MemberDetail"; // MemberDetail  
import MemberJoin from "./MemberJoin"; // MemberJoin  
import MemberModify from "./MemberModify"; // MemberModify  
import { fetchMem } from "../../services/api"; // API  
import "./MemberPage.css";

function MemberPage(props) {
  const [arr, setArr] = useState([]); //  
  const [selectedId, setSelectedId] = useState(null); //   ID
  const [showJoin, setShowJoin] = useState(false); //    
  const [showModify, setShowModify] = useState(false); //     

  const [totalMembers, setTotalMembers] = useState(0); //   

  //  
  const [idFilter, setIdFilter] = useState(""); //  ID 
  const [nameFilter, setNameFilter] = useState(""); //   
  const [typeFilter, setTypeFilter] = useState(""); //   
  const [levelFilter, setLevelFilter] = useState(""); //   
  const [startDate, setStartDate] = useState(""); //   
  const [endDate, setEndDate] = useState(""); //   

  //  
  const [currentPage, setCurrentPage] = useState(1); //  
  const itemsPerPage = 10; //   

  //  
  const types = ["", "", "", ""];
  const levels = ["", "", "", "", "VIP"];

    //   
    const fetchMembers = () => {
        //    endDate  
        const addEndDate = endDate
            ? new Date(new Date(endDate).setDate(new Date(endDate).getDate() + 1)).toISOString().split("T")[0]
            : "";

        const params = new URLSearchParams({
            id: idFilter,
            name: nameFilter,
            type: typeFilter,
            level: levelFilter,
            startDate,
            endDate: addEndDate,
            page: currentPage,
            limit: itemsPerPage,
        });

    fetchMem(params)
      .then((res) => {
        setArr(res.data.members);
        setTotalMembers(res.data.total);
      })
      .catch((err) => {
        console.error(" : ", err);
      });
  };

  useEffect(() => {
    fetchMembers();
  }, [currentPage]);

  const handleSearch = () => {
    setCurrentPage(1);
    fetchMembers();
  };

  const handleReset = () => {
    setIdFilter("");
    setNameFilter("");
    setTypeFilter("");
    setLevelFilter("");
    setStartDate("");
    setEndDate("");
    setCurrentPage(1);
    setArr([]);
    fetchMembers();
  };

  const totalPages = Math.ceil(totalMembers / itemsPerPage);

  const handleJoinClick = () => {
    setShowJoin(true); //   
    setSelectedId(null); //   
    setShowModify(false); //    
  };

  /*    */
  return (
    <div className="memberPage">
      <div
        className={`memberList ${selectedId || showJoin || showModify ? "half-width" : "full-width"}`}
      >
        {/*  ID,  ,         */}

        <h1>  </h1>

        {/*  */}
        <div className="filterBox">
          <div className="filterRow">
            <label className="filterItem">
               ID:
              <input
                type="text"
                value={idFilter}
                onChange={(e) => setIdFilter(e.target.value)}
              />
            </label>
            <label className="filterItem">
               :
              <input
                type="text"
                value={nameFilter}
                onChange={(e) => setNameFilter(e.target.value)}
              />
            </label>
            <label className="filterItem">
               :
              <select
                value={typeFilter}
                onChange={(e) => setTypeFilter(e.target.value)}
              >
                {types.map((type) => (
                  <option key={type} value={type}>
                    {type}
                  </option>
                ))}
              </select>
            </label>
            <label className="filterItem">
               :
              <select
                value={levelFilter}
                onChange={(e) => setLevelFilter(e.target.value)}
              >
                {levels.map((level) => (
                  <option key={level} value={level}>
                    {level}
                  </option>
                ))}
              </select>
            </label>
          </div>
          <div className="filterRow">
            <label className="filterItem">
               :
              <input
                type="date"
                value={startDate}
                onChange={(e) => setStartDate(e.target.value)}
              />
            </label>
            <label className="filterItem">
              ~
              <input
                type="date"
                value={endDate}
                onChange={(e) => setEndDate(e.target.value)}
              />
            </label>
            <div className="filterButtons">
              <button className="filterBtn" onClick={handleSearch}>
                
              </button>
              <button className="filterBtn" onClick={handleReset}>
                
              </button>
            </div>
          </div>
        </div>

        <div className="header">
          <div className="headercell"></div>
          <div className="headercell"></div>
          <div className="headercell"></div>
          <div className="headercell"></div>
          <div className="headercell"></div>
          <div className="headercell"></div>
        </div>

        <div className="main">
          {/*     */}
          {arr.map((st, i) => (
            <div
              className="datarow"
              key={i}
              onClick={() => {
                setSelectedId(st.id); //     ID 
                setShowJoin(false); //   
                setShowModify(false); //    
              }}
            >
              <div className="datacell">{st.id}</div>
              <div className="datacell">{st.name}</div>
              <div className="datacell">{st.phone_num}</div>
              <div className="datacell">{st.type}</div>
              <div className="datacell">{st.level}</div>
              <div className="datacell">{st.join_date}</div>
            </div>
          ))}
          <div className="postButtons">
            <button className="button-primary" onClick={handleJoinClick}>
              
            </button>
          </div>
        </div>

        {/*  */}
        <div className="pagination-container">
          {Array.from({ length: totalPages }, (_, index) => (
            <button
              key={`page-${index + 1}`}
              className={`pagination-link ${currentPage === index + 1 ? "active" : ""}`}
              onClick={() => setCurrentPage(index + 1)}
            >
              {index + 1}
            </button>
          ))}
        </div>
      </div>

      {/*    ,  ,     */}
      <div
        className={`memberDetail ${selectedId || showJoin || showModify ? "visible" : "hidden"}`}
      >
        {/*  ID     ,     */}
        {selectedId && !showModify && (
          <MemberDetail
            id={selectedId}
            fetchMembers={fetchMembers}
            setSelectedId={setSelectedId}
            setShowModify={setShowModify}
          />
        )}

        {/*     */}
        {showModify && (
          <MemberModify
            id={selectedId}
            setShowModify={setShowModify}
            fetchMembers={fetchMembers}
          />
        )}

        {/*     */}
        {showJoin && (
          <MemberJoin fetchMembers={fetchMembers} setShowJoin={setShowJoin} />
        )}
      </div>
    </div>
  );
}

export default MemberPage;
