import React from "react";
import { useNavigate } from "react-router-dom";
import { joinMem } from "../../services/api";
import "./MemberJoin.css";

function MemberJoin({ fetchMembers, setShowJoin }) {
  // fetchMembers :    
  // setShowJoin :         
  const navigate = useNavigate();

  //    
  function submitGo(event) {
    event.preventDefault();

        //   
        const frmData = new FormData(document.myFrm)
        const data = Object.fromEntries(frmData)

        //   
        joinMem(data)
        
        .then(res=>{
            alert(' .')
            fetchMembers(); //  
            setShowJoin(false); //   
        })
        
        .catch(err=>{
            console.log(' : ',err)
        })
    }
    
     // ""     
    const back = () => {
        setShowJoin(false); //   
        navigate('/member'); //  
    };
    
    
    /*    */
    return (
        <div className="member-join">
            <h1> </h1>
            <form name="myFrm" onSubmit={submitGo} className="join-form">
                
                <div className="jf">
                    <label>:</label>
                    <input type="text" name="id" required />
                </div>
                <div className="jf">
                    <label>:</label>
                    <input type="password" name="pw" required />
                </div>
                <div className="jf">
                    <label>:</label>
                    <input type="text" name="name" required />
                </div>
                <div className="jf">
                    <label>:</label>
                    <input type="text" name="nick" />
                </div>
                <div className="jf">
                    <label>:</label>
                    <input type="date" name="birth_date" />
                </div>
                <div className="jf">
                    <label>:</label>
                    <input type="text" name="phone_num" />
                </div>
                <div className="jf">
                    <label>:</label>
                    <input type="email" name="email" />
                </div>
                <div className="jf">
                    <label>:</label>
                    <input type="text" name="addr" />
                </div>
                
                <div className="jf">
                    <label> :</label>
                    <select name="role">
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                    </select>
                </div>

        <div className="jf">
          <label> :</label>
          <select name="type">
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
          </select>
        </div>

        <div className="jf">
          <label> :</label>
          <select name="level">
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value="VIP">VIP</option>
          </select>
        </div>

        <div className="jf">
          <label>:</label>
          <input type="text" name="reg_id" value="" required />
        </div>

        <div className="form-actions">
          <button type="button" onClick={back} className="cancel-btn">
            
          </button>
          <button type="submit" className="submit-btn">
            
          </button>
        </div>
      </form>
    </div>
  );
}

export default MemberJoin;
