import { Routes, Route, useLocation, useNavigate } from "react-router-dom";
import { useEffect, useState } from "react";
import pageFunction from "../hooks/pageFunction"; // useContent  
import LoginHome from "../pages/LoginHome"; // LoginHome 
import styles from '../admin.module.scss'

function HomePage(props) {
  const navigate = useNavigate(); //    useNavigate 
  const location = useLocation();
  const [loggedIn, setLoggedIn] = useState(false); //   
  // useContent      
  const content = pageFunction(location.pathname);
  const menuItems = [
    // { label: '  ', path: '/' },
    { label: '  ', path: '/board/registration' },
    { label: '  ', path: '/board/answer' },
    { label: ' ', path: '/board/promotion' },
    { label: ' ', path: '/member' },
    { label: '', path: '/products' },
    { label: '', path: '/products/add' },
    { label: '', path: '/option' },
    { label: ' ', path: '/keyword' },
    { label: '', path: '/order' },
    { label: '', path: '/refund' },
    { label: '', path: '/settle' },
    { label: '', path: '/iwantgohome' },
  ]


  useEffect(() => {
    const auth = sessionStorage.getItem("auth"); //     
    if (auth === "true") {
      setLoggedIn(true); //    
    } else {
      setLoggedIn(false); //   LoginHome 
      navigate("/"); //     
    }
  }, [navigate]);

  //   
  const handleMenuClick = (path) => {
    navigate(path); //   
  }

  //   
  const handleLogout = () => {
    //sessionStorage.removeItem('auth');  //     
    //sessionStorage.removeItem('user')
    sessionStorage.clear(); //   
    alert(" ");

    setLoggedIn(false); //   
    navigate("/"); //   
  };
  //      
  if (!loggedIn) {
    return <LoginHome setAuth={setLoggedIn} />;
  }

  return (
    <div className={styles.admin_wrap}>
      <nav>
        {/* <h1>  </h1> */}
        {/* URL /home  /boards  */}
        {/* <button onClick={() => navigate("/board/admin")}></button> */}
          <ul>
          <li onClick={() => navigate('/')} className={styles.home}><i className="fa-solid fa-house"></i></li>
          {menuItems.map((item, index) => (
            <li
              key={index}
              className={`menu-item ${location.pathname === item.path ? `${styles.active}` : ''}`}
              onClick={() => handleMenuClick(item.path)}
            >
              {item.label}
            </li>
          ))}
            <li onClick={handleLogout}></li>
          </ul>
      </nav>
      {/*      */}
      <div className={styles.admin_content}>{content}</div>
    </div>
  );
}

export default HomePage;
