import { useState, useEffect } from "react";
import { Link, useParams, useNavigate } from "react-router-dom";
import { detailMem } from "../../services/api";
import { deleteMem } from "../../services/api";
import "./MemberDetail.css";

function MemberDetail({ id, fetchMembers, setSelectedId, setShowModify }) {
  // fetchMembers :    
  // setSelectedId :   ID  
  // setShowModify :      
  const [st, setSt] = useState(null);
  const navigate = useNavigate();

  useEffect(() => {
    if (id) {
      detailMem(id)
        .then((res) => {
          setSt(res.data);
        })
        .catch((err) => {
          console.error(" : ", err);
        });
    }
  }, [id]);

  if (!st) {
    return <div>id </div>;
  }

  function back() {
    setSelectedId(null); // selectedId 
    navigate("/member"); // MemberPage 
  }

  //function fileGo(){
  //    if(st.upSystem){
  //        return <img src={`${bkURL}/fff/${st.upSystem}`}/>
  //    }
  //    return null
  //}

  function delGo() {
    deleteMem(id)
      //{data:{delUPfile : st.upSystem}}

      .then((res) => {
        // console.log("", res.data);
        alert(".");
        fetchMembers(); //  
        setSelectedId(null); //  
        navigate("/member");
      })
      .catch((err) => {
        console.error("", err);
      });

    console.log("delGO ");
  }

  /*     */
  return (
    <div className="mem-detail">
      <h1 className="title">  </h1>
      <div className="info">
        <div> : {st[0].name}</div>
        <div> : {st[0].id}</div>
        <div> : {st[0].pw}</div>
        <div> : {st[0].nick}</div>
        <div> : {st[0].birth_date}</div>
        <div> : {st[0].phone_num}</div>
        <div> : {st[0].email}</div>
        <div> : {st[0].addr}</div>
        <div> : {st[0].role}</div>
        <div> : {st[0].type}</div>
        <div> : {st[0].level}</div>
        <div> : {st[0].join_date}</div>
        <div> : {st[0].last_date}</div>
      </div>

      <div className="actions">
        <button onClick={back} className="list">
          
        </button>
        <button onClick={() => setShowModify(true)}></button>
        <button onClick={delGo} className="delete">
          
        </button>
      </div>

      {/*    */}
      <div className="mileage-section">
        <h1> </h1>
        <div className="header">
          <div className="headercell"></div>
          <div className="headercell"> </div>
          <div className="headercell"> </div>
          <div className="headercell"> </div>
          <div className="headercell"> </div>
        </div>
        <div className="main">
          {st.map((mileage, index) => (
            <div className="datarow" key={index}>
              <div className="datacell">{mileage.turn}</div>
              <div className="datacell">{mileage.earn_date}</div>
              <div className="datacell">{mileage.description}</div>
              <div className="datacell">{mileage.valid_date}</div>
              <div className="datacell">{mileage.change_val}</div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}

export default MemberDetail;
