import { selectProduct } from "../../services/api";
import { useEffect, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import "./ProductsList.css";

function ProductsList(props) {
  const [proList, setProlist] = useState([]); //
  const navigator = useNavigate();

  useEffect(() => {
    selectProduct()
      .then((res) => {
        setProlist(res.data);
      })
      .catch((err) => {
        console.error("    ", err);
      });
  }, []);

  function goAdd() {
    navigator("./products/add"); // 
  }

  return (
    <div className="memberList p-list">
      <div className="inquiry-section pro_detail">
        <h1></h1>
        <form>
          <table>
            <thead>
              <tr>
                <th>
                  <input type="checkbox" />
                </th>
                <th></th>
                {/* <th></th> */}
                <th></th>
                <th></th>
                <th></th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              {proList.map((st, i) => {
                let status = "OFF";
                // console.log(st.status === 1)
                if (st.status === 1) {
                  status = "ON";
                }
                // console.log(st.id)
                function goDetail() {
                  navigator(`/products/detail/${st.id}`); // 
                }

                return (
                  <tr key={i}>
                    <td>
                      <input type="checkbox" />
                    </td>
                    <td onClick={goDetail}>{i + 1}</td>
                    <td onClick={goDetail}>{st.name}</td>
                    <td onClick={goDetail}>{status}</td>
                    <td onClick={goDetail}>{st.quan}</td>
                    <td onClick={goDetail}>{st.reg_date}</td>
                  </tr>
                );
              })}
            </tbody>
          </table>
          <button type="button" className="write-btn" onClick={goAdd}>
            
          </button>
        </form>
      </div>
    </div>
  );
}

export default ProductsList;
