import { useState, useEffect } from "react";
import BoardPage from "../pages/board/BoardPage"; // BoardPage  
import BoardPromotionPage from "../pages/board/BoardPromotionPage"; // Promotion  
import OptionPage from "../pages/products/OptionPage"; // OptionPage  
import MemberPage from "../pages/member/MemberPage"; // MemberPage  
//import MileagePage from '../pages/member/MileagePage';  // MileagePage  
import OrderPage from "../pages/order/OrderPage"; // OrderPage  
import SettlePage from "../pages/order/SettlePage"; // SettlePage  
import KeywordPage from "../pages/products/KeywordPage";
import ProductsList from "../pages/products/ProductsList";
import ProductsAddPage from "../pages/products/ProductsAddPage";
import ProductsDetailPage from "../pages/products/ProductsDetailPage";
import ProductsModifyPage from "../pages/products/ProductsModifyPage";
import BoardRegistrationPage from "../pages/board/BoardRegistrationPage";
import BoardAnswerPage from "../pages/board/BoardAnswerPage";
import RefundPage from "../pages/order/RefundPage";
import LoginHome from "../pages/LoginHome"; // LoginHome 

//  :    
function useContent(path) {
  const [content, setContent] = useState(); //   

  useEffect(() => {
    const auth = sessionStorage.getItem("auth"); //    
    if (auth !== "true" && path.startsWith("/home")) {
      //     
      setContent(<LoginHome />);
    } else {
      //    
      if (!path || path === "/" || path === "/home") {
        setContent(<div> </div>);
      } else if (path === "/boards") {
        setContent(<BoardPage />); // 
      } else if (path === "/board/promotion") {
        setContent(<BoardPromotionPage />); //  
      } else if (path === "/board/answer") {
        setContent(<BoardAnswerPage />); //  
      } else if (path === "/board/registration") {
        setContent(<BoardRegistrationPage />); //  
      } else if (path === "/products") {
        setContent(<ProductsList />); // 
      } else if (path === "/products/detail") {
        setContent(<ProductsDetailPage />); // 
      } else if (path === "/products/add") {
        setContent(<ProductsAddPage />); // 
      } else if (path === "/option") {
        setContent(<OptionPage />); // 
      } else if (path === "/keyword") {
        setContent(<KeywordPage />); // 
      } else if (path === "/member") {
        setContent(<MemberPage />); // 
      // } else if (path === "/mileage") {
      //   setContent(<MileagePage />); // 
      // } else if (path === "/deposit") {
      //   setContent(<DepositPage />); // 
      } else if (path === "/order") {
        setContent(<OrderPage />); // 
      } else if (path === "/settle") {
        setContent(<SettlePage />); // 
      } else if (path === "/refund") {
        setContent(<RefundPage />); // 
      } else if (path === "/iwantgohome") {
        setContent(<div>~</div>); // 
      }

      // 
      const matchProductDetailId = path.match(/^\/products\/detail\/([^/]+)$/);
      const matchProductModifyId = path.match(/^\/products\/modify\/([^/]+)$/);
      const matchOptionDetailCode = path.match(/^\/option\/value\/([^/]+)$/);
      const matchKeywordDetailCode = path.match(/^\/keyword\/detail\/([^/]+)$/);
      if (matchProductDetailId) {
        const productId = matchProductDetailId[1];
        // console.log('productId,',productId)
        setContent(<ProductsDetailPage id={productId} />); // 
        // console.log('Extracted product ID:', productId);
      } else if (matchProductModifyId) {
        const productId = matchProductModifyId[1];
        setContent(<ProductsModifyPage id={productId} />); // 
      }
    }
  }, [path]); // path   

  return content; //  
}

export default useContent;
