import React, { useEffect, useState } from "react";
import { fetchBoards, insertBoards } from "../../services/api"; // API  
import { Link } from "react-router-dom";

function BoardPage() {
  const [boards, setBoards] = useState([]); // boards  
  const [file, setFile] = useState(null); //   

  //   
  useEffect(() => {
    fetchBoards()
      .then((res) => {
        setBoards(res.data); //    boards  
      })
      .catch((error) => {
        console.error("Error fetching boards:", error); //  
      });
  }, []); //     

  //   
  const handleFileChange = (e) => {
    const selectedFile = e.target.files[0];
    setFile(selectedFile);
  };

  //   
  const submitGo = (e) => {
    e.preventDefault(); //  

    //     
    if (!file) {
      alert(" !");
      return;
    }

    // FormData     
    const frmData = new FormData();
    frmData.append("upfile", file);

    //   API 
    insertBoards(frmData)
      .then((res) => {
        alert("  !");
        setFile(null); //   
      })
      .catch((error) => {
        console.error("Error uploading file:", error);
        alert("  !");
      });
  };

  return (
    <div>
      <h1>()</h1>
      <form name="myFrm" onSubmit={submitGo}>
        <table border="1">
          <thead>
            <tr>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
            </tr>
          </thead>
          <tbody>
            {boards.map((item, key) => (
              <tr key={key}>
                <td>{item.hakgi}</td>
                <td>{item.name}</td>
                <td>{item.pid}</td>
                <td>{item.reg_date}</td>
              </tr>
            ))}

            {/*    */}
            <tr>
              <td></td>
              <td>
                <input type="file" name="upfile" onChange={handleFileChange} />
              </td>
            </tr>
            <tr>
              <td colSpan="4">
                <Link to="#"></Link>
                <input type="submit" value="" />
              </td>
            </tr>
          </tbody>
        </table>
      </form>
    </div>
  );
}

export default BoardPage;
