import React, { useState, useEffect } from "react";
import Modal from "react-modal";
import {
  fetchRefunds,
  updateRefundStatus,
  updateRefundReason,
} from "../../services/api"; // API  
import "./OrderPage.css";

Modal.setAppElement("#root");

function RefundPage() {
  const [refundData, setRefundData] = useState([]);
  const [totalRefunds, setTotalRefunds] = useState(0);

  const [selectedStatus, setSelectedStatus] = useState("");
  const [currentPage, setCurrentPage] = useState(1); //  
  const [startDate, setStartDate] = useState(""); // 
  const [endDate, setEndDate] = useState(""); // 
  const [filters, setFilters] = useState(null); //  

  const [modalIsOpen, setModalIsOpen] = useState(false); //  
  const [selectedRefundId, setSelectedRefundId] = useState(null); //   ID
  const [rejectionReason, setRejectionReason] = useState(""); //  
  const [rejectedRefunds, setRejectedRefunds] = useState({}); //   

  const itemsPerPage = 5; //    
  const statuses = ["", "", "", "", ""];
  const rejectionReasons = [" ", " ", ""];

  //   
  const fetchRefundData = async () => {
    if (!filters) return; //     
    try {
      const params = {
        ...filters,
        page: currentPage,
        limit: itemsPerPage,
      };

      const response = await fetchRefunds(params);
      setRefundData(response.data.refunds || []);
      setTotalRefunds(response.data.total || 0);
    } catch (error) {
      console.error("Error fetching refund data:", error);
    }
  };

  useEffect(() => {
    fetchRefundData();
  }, [filters, currentPage]);

  const handleSearch = () => {

    let adjustedEndDate = "";
  if (endDate) {
    const dateObj = new Date(endDate);
    dateObj.setDate(dateObj.getDate() + 1); //  
    adjustedEndDate = dateObj.toISOString().split("T")[0]; // YYYY-MM-DD  
  }

  setFilters({
    status: selectedStatus,
    startDate,
    endDate: adjustedEndDate, //  endDate 
  });
  setCurrentPage(1); //    
};

  const handleReset = () => {
    setSelectedStatus("");
    setStartDate("");
    setEndDate("");
    setFilters(null); //  
    setRefundData([]); //  
    setTotalRefunds(0);
    setCurrentPage(1); //  
  };

  const handleOpenModal = (refundId) => {
    setSelectedRefundId(refundId);
    setModalIsOpen(true);
  };

  const handleCloseModal = () => {
    setModalIsOpen(false);
    setRejectionReason("");
  };

  const handleConfirmReject = async () => {
    try {
      await updateRefundReason(selectedRefundId, rejectionReason); // API 
      setRejectedRefunds((prev) => ({
        ...prev,
        [selectedRefundId]: rejectionReason,
      }));
      alert("  .");
      fetchRefundData();
    } catch (error) {
      console.error("Error updating rejection reason:", error);
      alert("  .");
    } finally {
      handleCloseModal();
    }
  };

  const handleStatusChange = async (refundId, currentStatus) => {
    try {
      await updateRefundStatus(refundId, currentStatus); //   
      alert("  .");
      fetchRefundData(); //  
    } catch (error) {
      console.error("Error updating status:", error);
      alert("   .");
    }
  };

  const totalPages = Math.ceil(totalRefunds / itemsPerPage); //    

  const handlePageChange = (page) => {
    setCurrentPage(page);
  };

  return (
    <div className="inquiry-section">
      <div className="filter-header">
        <h1>  </h1>
      </div>

      <div className="mypage-date-check">
        <div className="mypage-date-period">
          <span className="mypage-period-text"></span>
        </div>

        <div className="mypage-date-box">
          <input
            type="date"
            className="mypage-first-date"
            value={startDate}
            onChange={(e) => setStartDate(e.target.value)}
          />
          <span>~</span>
          <input
            type="date"
            className="mypage-last-date"
            value={endDate}
            onChange={(e) => setEndDate(e.target.value)}
          />
        </div>

        <div className="filter-container">
          <label>
            :
            <select
              value={selectedStatus}
              onChange={(e) => setSelectedStatus(e.target.value)}
              className="filter-select"
            >
              {statuses.map((status) => (
                <option key={status} value={status}>
                  {status}
                </option>
              ))}
            </select>
          </label>
        </div>

        <div
          className="mypage-date-search"
          style={{ display: "flex", gap: "10px", marginLeft: "10px" }}
        >
          <button
            className="mypage-date-search-btn"
            onClick={handleSearch}
            style={{ flex: "1" }}
          >
            
          </button>
          <button
            className="mypage-date-reset-btn"
            onClick={handleReset}
            style={{ flex: "1", backgroundColor: "#ff4d4f", color: "#fff" }}
          >
            
          </button>
        </div>
      </div>

      <div className="table_container">
        {refundData.length > 0 ? (
          <table>
            <thead>
              <tr>
                <th> ID</th>
                <th> ID</th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              {refundData.map((refund) => (
                <tr key={refund.refund_id}>
                  <td>{refund.refund_id}</td>
                  <td>{refund.member_id}</td>
                  <td>{refund.status}</td>
                  <td>{refund.amount.toLocaleString()}</td>
                  <td>{refund.date}</td>
                  <td>
                    {rejectedRefunds[refund.refund_id] ? (
                      <>
                        <span style={{ color: "red" }}></span>
                        <br />
                        <span>: {rejectedRefunds[refund.refund_id]}</span>
                      </>
                    ) : refund.status === "" ||
                      refund.status === "" ? (
                      <>
                        <button
                          onClick={() =>
                            handleStatusChange(refund.refund_id, refund.status)
                          }
                        >
                          
                        </button>
                        <button
                          onClick={() => handleOpenModal(refund.refund_id)}
                        >
                          
                        </button>
                      </>
                    ) : null}
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        ) : (
          <p className="no-orders-message">
                .
          </p>
        )}
      </div>

      <div className="pagination-container">
        {Array.from({ length: totalPages }, (_, index) => (
          <button
            key={`page-${index + 1}`}
            className={currentPage === index + 1 ? "current" : ""}
            onClick={() => handlePageChange(index + 1)}
          >
            {index + 1}
          </button>
        ))}
      </div>

      <Modal
        isOpen={modalIsOpen}
        onRequestClose={handleCloseModal}
        style={{
          content: {
            top: "50%",
            left: "50%",
            transform: "translate(-50%, -50%)",
            width: "300px",
          },
        }}
      >
        <h2>  </h2>
        <select
          value={rejectionReason}
          onChange={(e) => setRejectionReason(e.target.value)}
        >
          <option value=""> </option>
          {rejectionReasons.map((reason) => (
            <option key={reason} value={reason}>
              {reason}
            </option>
          ))}
        </select>
        <button onClick={handleConfirmReject} disabled={!rejectionReason}>
          
        </button>
        <button onClick={handleCloseModal}></button>
      </Modal>
    </div>
  );
}

export default RefundPage;
