import { useState, useEffect } from "react";
import {
  insertProduct,
  selectProductDetail,
  selectOption,
  selectOptionVal,
  selectKeyword,
  selectKeywordVal,
} from "../../services/api"; // API  
import { Link, useNavigate } from "react-router-dom";
// import OptionSelect from './OptionSelect';
// import KeywordSelect from './KeywordSelect';
import "./ProductsAddPage.css";

function ProductsModifyPage({ id, myData, code }) {
  const navigator = useNavigate();
  const [proList, setProlist] = useState({}); //

  // state
  const [detailFile, setDTFile] = useState(null); //  
  const [proFile, setFile] = useState([]); // 

  // -    
  const [isLoading, setIsLoading] = useState(true);

  //+
  const [option01, setOption01] = useState([]); // ()
  const [option02, setOption02] = useState(""); //  
  const [keyword01, setKeyword01] = useState([]); // ()
  const [keyword02, setKeyword02] = useState(""); //  
  const [opVal, setOpVal] = useState([]); //   
  const [keyVal, setKeyVal] = useState([]); //   
  const [selectedOptions, setSelectedOptions] = useState([]);
  const [selectedKeywords, setSelectedKeywords] = useState([]);

  useEffect(() => {
    //  
    selectProductDetail(id)
      .then((res) => {
        const data = res.data;
        setProlist(data);
        setSelectedOptions(data.selectedOptions); //   
        setSelectedKeywords(data.selectedKeywords); //   
        setIsLoading(false); //
      })
      .catch((err) => {
        console.error("    ", err);
        setIsLoading(false); //   
      });
  }, [id]);
  const product = proList.ret; // 
  console.log(product);

  //  
  const upDTFile = (e) => {
    let pickDTFiles = e.target.files[0];
    setDTFile(pickDTFiles);
  };

  //  
  const upMultiFiles = (e) => {
    let selectFiles = e.target.files;
    setFile(selectFiles);
  };

  // state
  const [orPrice, setOrPrice] = useState(0); //
  const [disV, setDisV] = useState(0); //
  const [fnPrice, setFnPrice] = useState(0); //

  // +
  function fnPriceChange() {
    // 
    let newFnPrice = Math.round(orPrice - orPrice * (disV / 100));
    //1   10      10 
    newFnPrice = Math.round(newFnPrice / 10) * 10;

    if (orPrice && disV > 0) {
      //     
      setFnPrice(newFnPrice); // 
      if (newFnPrice < 0 || disV > 100) {
        //   100  
        alert("  .");
        return setDisV(0);
      } 
      // else if (disV == 100) {
      //   // 100  
      //   let checConfirm = confirm(" 100%  ?");
      //   if (!checConfirm) {
      //     return setDisV(0);
      //   }
      // }
    } else if (!disV || disV == 0) {
      //  0 
      let newOrPrice = Math.ceil(orPrice / 10) * 10;
      if (orPrice % 10 <= 4) {
        newOrPrice = Math.round(orPrice / 10) * 10;
      }
      setFnPrice(newOrPrice); // 
    }
  }

  // ,    
  const orPriceChange = (e) => {
    //
    const newPrice = e.target.value; //  
    setOrPrice(newPrice); //   
  };
  const disVChange = (e) => {
    //
    const newDisV = e.target.value;
    setDisV(newDisV);
  };

  // ,  fnPriceChange   
  useEffect(() => {
    fnPriceChange();
  }, [orPrice, disV]);

  // ( )
  //  
  useEffect(() => {
    selectOption(myData)
      .then((res) => {
        setOption01(res.data);
      })
      .catch((err) => {
        console.error("    ", err);
      });
  }, [myData]);

  // 
  useEffect(() => {
    if (option02) {
      //     
      //    option02 code   
      const selectedOption = option01.find(function (opt) {
        // option02  name 
        return opt.name === option02;
      });
      if (selectedOption) {
        const { code } = selectedOption; //   code  
        // console.log(code)
        selectOptionVal(code)
          .then((res) => {
            setOpVal(res.data); //  
          })
          .catch((err) => {
            console.error("  ", err);
          });
      }
    }
  }, [option02, option01]);
  const optionClick = (e) => {
    setOption02(e.target.value); //   
  };

  // 
  const checkClick = (e) => {
    const { value, checked } = e.target;
    setSelectedOptions((prevOptions) => {
      if (checked) {
        //     
        return [...(prevOptions || []), value];
      } else {
        //     
        return (prevOptions || []).filter((v) => v !== value);
      }
    });
  };

  //  
  useEffect(() => {
    selectKeyword(myData)
      .then((res) => {
        setKeyword01(res.data);
      })
      .catch((err) => {
        console.error("    ", err);
      });
  }, [myData]);
  // 
  useEffect(() => {
    if (keyword02) {
      //     
      //    keyword02 code   
      const selectedKeyword = keyword01.find(function (opt) {
        // keyword02  name 
        return opt.name === keyword02;
      });
      if (selectedKeyword) {
        const { code } = selectedKeyword; //   code  
        // console.log(code)
        selectKeywordVal(code)
          .then((res) => {
            setKeyVal(res.data); //  
          })
          .catch((err) => {
            console.error("  ", err);
          });
      }
    }
  }, [keyword02, keyword01]);

  // 
  const keywordClick = (e) => {
    setKeyword02(e.target.value); //   
  };
  // 

  const checkClick02 = (e) => {
    const { value, checked } = e.target;
    setSelectedKeywords((prevKeywords) => {
      if (checked) {
        //     (   )
        return [...(prevKeywords || []), value];
      } else {
        //     
        return (prevKeywords || []).filter((v) => v !== value);
      }
    });
  };

  function getSelectedDetails() {

    //  
    if (!selectedOptions || !Array.isArray(selectedOptions)) {
      console.error("selectedOptions   .");
      return []; //     
    }
  
    if (!selectedKeywords || !Array.isArray(selectedKeywords)) {
      console.error("selectedKeywords   .");
      return []; //     
    }
  
    if (!opVal || !Array.isArray(opVal)) {
      console.error("opVal   .");
      return []; //     
    }
  
    if (!keyVal || !Array.isArray(keyVal)) {
      console.error("keyVal   .");
      return []; //     
    }

    
    const optionDetails = selectedOptions
      .map((option) => {
        const optionData = opVal.find((opt) => opt.name === option);
        if (!optionData) {
          console.warn(`  : ${option}`);
          return null; //   null 
        }
        return {
          main_code: optionData.main_code, //   
          code: optionData.code, //   
          type: 1, //  
          price: optionData.price, // 
          status: optionData.status, //  
        };
      })
      .filter((option) => option !== null); // null 

    const keywordDetails = selectedKeywords
      .map((keyword) => {
        const keywordData = keyVal.find((kw) => kw.name === keyword);
        if (!keywordData) {
          console.warn(`  : ${keyword}`);
          return null; //   null 
        }
        return {
          main_code: keywordData.main_code, //   
          code: keywordData.code, //   
          type: 0, //  
          price: keywordData.price, //  
          status: keywordData.status, //  
        };
      })
      .filter((keyword) => keyword !== null); // null 

    return [...optionDetails, ...keywordDetails]; // +
  }

  function proSubmit(e) {
    e.preventDefault();
    //      
    if(selectedOptions){
      if (selectedOptions.length === 0) {
        alert("  .");
        return; //  
      }
      if (selectedKeywords.length === 0) {
        alert("  .");
        return; //  
      }
    }

    const frmData = new FormData(document.proFrm);
    //     
    if (detailFile) {
      frmData.append("upfile", detailFile);
    }

    //      
    Array.from(proFile).forEach((file) => {
      frmData.append("upMultiFiles", file);
    });

    //      
    const selectedDetails = getSelectedDetails();
    selectedDetails.forEach((detail) => {
      if (detail) {
        // detail null   
        frmData.append("details", JSON.stringify(detail)); // JSON  
      }
    });

    insertProduct(frmData)
      .then((res) => {
        console.log("", res.data);
        alert(".");
        navigator(`/products`); //  
      })
      .catch((err) => {
        console.log(" : ", err.res ? err.res.data : err.message);
        alert("");
      });
  }
  function goList() {
    navigator(`/products`);
  }
  function goHome() {
    navigator("/");
  }
  if (isLoading) {
    //   
    return <div> ...</div>;
  }

  return (
    <div>
      <form name="proFrm" className="proFrm" onSubmit={proSubmit}>
        <div className="join_base_section memberList">
          <div className="join_base_wrap">
            <div className="filter-header">
              <h1>  </h1>
            </div>
            <br />
            <table>
              <tbody>
                <tr>
                  <td className="inputHead">
                    <i className="fa-solid fa-square fa-2xs" />
                    
                  </td>
                  <td>
                    <input
                      type="text"
                      name="name"
                      defaultValue={product.name}
                      required
                    />
                  </td>
                </tr>
                <tr>
                  <td className="inputHead">
                    <i className="fa-solid fa-square fa-2xs" />
                    
                  </td>
                  <td>
                    <input
                      type="file"
                      name="upMultiFiles"
                      onChange={upMultiFiles}
                      multiple
                    />
                  </td>
                </tr>
                <tr>
                  <td className="inputHead"></td>
                  <td>
                    <input type="file" name="upfile" onChange={upDTFile} />
                  </td>
                </tr>
                <tr>
                  <td className="inputHead">
                    <i className="fa-solid fa-square fa-2xs" />
                      
                  </td>
                  <td className="hasUnit">
                    <input
                      type="number"
                      name="quan"
                      defaultValue={product.quan}
                      required
                    />
                    <span></span>
                  </td>
                </tr>
                <tr>
                  <td className="inputHead"></td>
                  <td>
                    <select onChange={optionClick}>
                      {/* <select onChange={checkClick} value={setOption}> */}
                      <option value="none"> </option>
                      {option01.map((st, i) => (
                        // console.log(st.name)
                        <option key={i} value={st.name}>
                          {st.name}
                        </option>
                      ))}
                    </select>
                  </td>
                </tr>
                {option02 && Array.isArray(opVal) && opVal.length > 0 && ( //      
                  <tr>
                    <td className="inputHead"> </td>
                    <td>
                      {opVal.length > 0 ? ( //   1  =  
                        opVal.map((st, i) => (
                          <label key={i}>
                            <input
                              type="checkbox"
                              name="option"
                              value={st.name}
                              checked={Array.isArray(selectedOptions) && selectedOptions.includes(st.name)}
                              onChange={checkClick}
                            />
                            {st.name}
                          </label>
                        ))
                      ) : (
                        <span>  .</span> // 
                      )}
                    </td>
                  </tr>
                )}
                <tr>
                  <td className="inputHead">  </td>
                  <td>
                    <select onChange={keywordClick}>
                      <option value="none"> </option>
                      {keyword01.map((st, i) => (
                        // console.log(st.name)
                        <option key={i} value={st.name}>
                          {st.name}
                        </option>
                      ))}
                    </select>
                  </td>
                </tr>
                {keyword02 && Array.isArray(keyVal) && keyVal.length > 0 && ( //      
                  <tr>
                    <td className="inputHead"> </td>
                    <td>
                      {keyVal.length > 0 ? ( //   1  =  
                        keyVal.map((st, i) => (
                          <label key={i}>
                            <input
                              type="checkbox"
                              name="keyword"
                              value={st.name}
                              checked={Array.isArray(selectedKeywords) && selectedKeywords.includes(st.name)}
                              onChange={checkClick02}
                            />
                            {st.name}
                          </label>
                        ))
                      ) : (
                        <span>  .</span> // 
                      )}
                    </td>
                  </tr>
                )}
                <tr>
                  <td className="inputHead">
                    <i className="fa-solid fa-square fa-2xs" />
                     
                  </td>
                  <td className="hasRadio">
                    <label>
                      <input
                        type="radio"
                        name="status"
                        defaultChecked={product.status === 1}
                        value={1}
                      />
                      ON
                    </label>
                    <label>
                      <input
                        type="radio"
                        name="status"
                        defaultChecked={product.status === 0}
                        value={0}
                      />
                      OFF
                    </label>
                  </td>
                </tr>
                <tr>
                  <td className="inputHead"></td>
                  <td>
                    <textarea name="benefit" defaultValue={product.benefit} />
                  </td>
                </tr>
                <tr>
                  <td className="inputHead">
                    <i className="fa-solid fa-square fa-2xs" />
                     
                  </td>
                  <td className="hasUnit">
                    <input
                      type="number"
                      name="price"
                      value={product.price}
                      onChange={orPriceChange}
                      required
                    />
                    <span></span>
                  </td>
                </tr>
                <tr>
                  <td className="inputHead"></td>
                  <td className="hasUnit">
                    <input
                      type="number"
                      name="discount"
                      value={product.discount}
                      onChange={disVChange}
                    />
                    <span>%</span>
                  </td>
                </tr>
                <tr>
                  <td className="inputHead">
                    <i className="fa-solid fa-square fa-2xs" />
                     
                  </td>
                  <td className="hasUnit">
                    <input type="number" readOnly value={fnPrice || "0"} />
                    <span></span>
                  </td>
                </tr>
              </tbody>
            </table>
            <input type="submit" className="submit-btn" value="" />
            <div className="btn-wrap">
              <button type="button" className="write-btn" onClick={goList}>
                
              </button>
              <button type="button" className="home-btn" onClick={goHome}>
                
              </button>
            </div>
          </div>
        </div>
      </form>
    </div>
  );
}

export default ProductsModifyPage;
