import React, { useState, useEffect } from "react";
import "./OrderPage.css";
import { fetchOrders, updateOrderStatus } from "../../services/api"; //  updateOrderStatus 

function OrderPage() {
  const [orders, setOrders] = useState([]);
  const [totalOrders, setTotalOrders] = useState(0);
  const [selectedStatus, setSelectedStatus] = useState("");
  const [selectedPayMethod, setSelectedPayMethod] = useState("");
  const [selectedConfirmation, setSelectedConfirmation] = useState("");
  const [searchTerm, setSearchTerm] = useState("");
  const [currentPage, setCurrentPage] = useState(1);
  const [startDate, setStartDate] = useState(""); //  
  const [endDate, setEndDate] = useState(""); //  
  const [filters, setFilters] = useState(null); //      
  const itemsPerPage = 5;

  const statuses = ["", "", "", ""];
  const payMethods = ["", "", ""];
  const confirmations = ["", "", ""];

  const fetchOrderList = async () => {
    if (!filters) return; //      
    try {
      const params = {
        ...filters,
        page: currentPage,
        limit: itemsPerPage,
      };

      const response = await fetchOrders(params);
      setOrders(response.data.orders || []);
      setTotalOrders(response.data.total || 0);
    } catch (error) {
      console.error("Error fetching orders:", error);
    }
  };

  useEffect(() => {
    fetchOrderList();
  }, [currentPage, filters]);

  const handleSearch = () => {
    // endDate  ,  
    let adjustedEndDate = "";
    if (endDate) {
      const dateObj = new Date(endDate);
      dateObj.setDate(dateObj.getDate() + 1); //  
      adjustedEndDate = dateObj.toISOString().split("T")[0]; // YYYY-MM-DD  
    }
  
    setFilters({
      status: selectedStatus,
      payMethod: selectedPayMethod,
      confirmation: selectedConfirmation,
      search: searchTerm,
      startDate, //  
      endDate: adjustedEndDate, //  endDate 
    });
    setCurrentPage(1); //    
  };

  // ,  
  const handleStatusChange = async (orderId, newStatus) => {
    try {
      const response = await updateOrderStatus(orderId, newStatus); //  : axios    updateOrderStatus 
      if (response.data.success) {
        alert(response.data.message);
        fetchOrderList(); //    
      }
    } catch (error) {
      console.error("   :", error);
    }
  };

  const handleReset = () => {
    setSelectedStatus("");
    setSelectedPayMethod("");
    setSelectedConfirmation("");
    setSearchTerm("");
    setStartDate("");
    setEndDate("");
    setFilters(null); //  
    setOrders([]); //   
    setTotalOrders(0);
    setCurrentPage(1);
  };

  const totalPages = Math.ceil(totalOrders / itemsPerPage);

  return (
    <div className="inquiry-section">
      <div className="filter-header">
        <h1>  </h1>
      </div>

      <div className="mypage-date-check">
        <div className="mypage-date-period">
          <span className="mypage-period-text"></span>
        </div>

        <div className="mypage-date-box">
          <input
            type="date"
            className="mypage-first-date"
            value={startDate}
            onChange={(e) => setStartDate(e.target.value)}
          />
          <span>~</span>
          <input
            type="date"
            className="mypage-last-date"
            value={endDate}
            onChange={(e) => setEndDate(e.target.value)}
          />
        </div>

        <div className="filter-container">
          <label style={{ marginLeft: "10px" }}>
             :
            <select
              value={selectedStatus}
              onChange={(e) => setSelectedStatus(e.target.value)}
              className="filter-select"
            >
              {statuses.map((status) => (
                <option key={status} value={status}>
                  {status}
                </option>
              ))}
            </select>
          </label>

          <label style={{ marginLeft: "10px" }}>
             :
            <select
              value={selectedPayMethod}
              onChange={(e) => setSelectedPayMethod(e.target.value)}
              className="filter-select"
            >
              {payMethods.map((method) => (
                <option key={method} value={method}>
                  {method}
                </option>
              ))}
            </select>
          </label>

          <label style={{ marginLeft: "10px" }}>
             :
            <select
              value={selectedConfirmation}
              onChange={(e) => setSelectedConfirmation(e.target.value)}
              className="filter-select"
            >
              {confirmations.map((confirm) => (
                <option key={confirm} value={confirm}>
                  {confirm}
                </option>
              ))}
            </select>
          </label>
        </div>

        {/*  */}
        <div
          className="mypage-date-search"
          style={{ display: "flex", gap: "10px", marginLeft: "10px" }}
        >
          <button
            className="mypage-date-search-btn"
            onClick={handleSearch}
            style={{ flex: "1" }}
          >
            
          </button>
          <button
            className="mypage-date-reset-btn"
            onClick={handleReset}
            style={{ flex: "1", backgroundColor: "#ff4d4f", color: "#fff" }}
          >
            
          </button>
        </div>
      </div>

      <div className="table_container">
        {orders.length > 0 ? (
          <table>
            <thead>
              <tr>
                <th> ID</th>
                <th> ID</th>
                <th></th>
                <th></th>
                <th></th>
                <th> </th>
                <th></th>
                <th> </th>
              </tr>
            </thead>
            <tbody>
              {orders.map((order) => (
                <tr key={order.order_id}>
                  <td>{order.order_id || "N/A"}</td>
                  <td>{order.member_id || "N/A"}</td>
                  <td>{order.status || "N/A"}</td>
                  <td>{order.payMethod || "N/A"}</td>
                  <td>{order.isConfirmed || "N/A"}</td>
                  <td>
                    {order.total ? `${order.total.toLocaleString()}` : "N/A"}
                  </td>
                  <td>{order.order_date || "N/A"}</td>
                  <td>
                    {order.status === "" && (
                      <button
                        onClick={() =>
                          handleStatusChange(order.order_id, "")
                        }
                      >
                        
                      </button>
                    )}
                    {order.status === "" && (
                      <button
                        onClick={() =>
                          handleStatusChange(order.order_id, "")
                        }
                      >
                        
                      </button>
                    )}
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        ) : (
          <p className="no-orders-message">   .</p>
        )}
      </div>

      <div className="pagination-container">
        {Array.from({ length: totalPages }, (_, index) => (
          <button
            key={`page-${index + 1}`}
            className={currentPage === index + 1 ? "current" : ""}
            onClick={() => setCurrentPage(index + 1)}
          >
            {index + 1}
          </button>
        ))}
      </div>
    </div>
  );
}

export default OrderPage;
