import {
  selectProductDetail,
  selectImageLoad,
  selectDetail,
  selectOption,
  selectDetailInfo,
  selectOptionVal,
  selectKeyword,
  selectKeywordVal,
} from "../../services/api";
import { useEffect, useState } from "react";
import { Link, useNavigate, useParams } from "react-router-dom";
import "./ProductsDetailPage.css";

function ProductsDetailPage({ id, myData, code }) {
  //pageFunction productId  
  const { main_code } = useParams();
  const navigator = useNavigate();

  //  (    )
  const [loading, setLoading] = useState(true);
  const [proList, setProlist] = useState({}); //
  const [dtList, setDtList] = useState([]); // 
  const [opList, setOpList] = useState([]); // 
  const [keyList, setKeyList] = useState([]); // 
  const [keyValList, setKeyvalList] = useState([]); // 

  const [mainCode, setmainCode] = useState([]); //
  const [dtcode, setdtcode] = useState([]); //
  const [dtBack, setDtBack] = useState({ mainCode: [], dtCode: [] }); //   


  //  
  useEffect(() => {
    if (!id) {
      // console.log("id ");
      return;
    }
    setLoading(true); // 

    //  
    selectProductDetail(id)
      .then((res) => {
        setProlist(res.data);
        // setLoading(false)//
      })
      .catch((err) => {
        console.error("    ", err);
        // setLoading(false)//   
      });

    //  
    selectDetail(main_code)
      .then((res) => {setDtList(res.data || []);})
      .catch((err) => {console.error("    ", err);});

    // 
    selectOption(myData)
      .then((res) => {setOpList(res.data || []);})
      .catch((err) => {console.error("   ", err);});

    // 
    selectOptionVal(code)
      .then((res) => {
         if (res.data) {
          setOpVal(res.data); //     
        } else {
          console.error("   .");
        }
      })
      .catch((err) => {console.error(" API  :", err)});

    //  
    selectKeyword(myData)
      .then((res) => {setKeyList(res.data || [])})
      .catch((err) => {console.error("    ", err)});

    // 
    selectKeywordVal(code)
      .then((res) => {setKeyvalList(res.data || [])})
      .catch((err) => {console.error("   ", err)});

    //   
    selectDetailInfo(main_code, id)
      .then((res) => {
        setmainCode(res.data.main_code);
        setdtcode(res.data.code);
        setDtBack({
          mainCode: res.data.main_code,
          dtCode: res.data.code,
          name: res.data.name,
        });
      })
      .catch((err) => {console.error("   ", err)});
    setLoading(false); //  
  }, [id, main_code, code, myData]);

  //
  const [option01, setOption01] = useState([]); // ()
  const [option02, setOption02] = useState(""); //  
  const [opVal, setOpVal] = useState([]); //   
  const [selectedOptions, setSelectedOptions] = useState([]); //   

  //  
  useEffect(() => {
    selectOption(myData)
      .then((res) => {setOption01(res.data)})
      .catch((err) => {console.error("    ", err)});
  }, [myData]);

  // 
  useEffect(() => {
    if (myData) {
      //   code  
      const selectedOption = dtcode;
      selectOptionVal(selectedOption)
        .then((res) => {
          setOpVal(res.data); //  
        })
        .catch((err) => {
          console.error("  ", err);
        });
    }
  }, [option02, option01]);

  //  
  if (loading) {
    //false 
    return <div>  </div>;
  }
  if (!proList.ret) {
    //  (proList.ret = false)
    return <div> </div>;
  }

  //  
  const proData = proList.ret;

  /////////////
  let orPrice = proData.price || 0;
  let disV = proData.discount || 0;
  let fnPrice = 0;
  // console.log({id})

  if (disV) {
    // let newFnPrice = Math.round(orPrice - orPrice * (disV / 100));
    // fnPrice = newFnPrice;
    fnPrice = Math.round(orPrice - orPrice * (disV / 100));
    // }else if(disV == 0 || disV == null){
  } else {
    disV = 0;
    fnPrice = orPrice;
  }

  ////////////// 
  function statusChange() {
    if (proData.status === 1) {
      return (
        <td className="hasRadio">
          <label className="active">
            <input
              type="radio"
              name="status"
              value={1 || ""}
              disabled
              checked
            />
            ON
          </label>
          <label>
            <input type="radio" name="status" value={0 || ""} disabled />
            OFF
          </label>
        </td>
      );
    } else if (proData.status === 0) {
      return (
        <td className="hasRadio">
          <label>
            <input type="radio" name="status" value={0 || ""} disabled />
            ON
          </label>
          <label className="active">
            <input
              type="radio"
              name="status"
              value={1 || ""}
              disabled
              checked
            />
            OFF
          </label>
        </td>
      );
    } else if (proData.status === null) {
      return (
        <td className="hasRadio">
          <label>
            <input type="radio" name="status" value={0 || ""} disabled />
            ON
          </label>
          <label>
            <input type="radio" name="status" value={1 || ""} disabled />
            OFF
          </label>
        </td>
      );
    }
  }

  ///////////
  function goModify() {
    navigator(`/products/modify/${id}`);
  }
  function goList() {
    navigator("/products");
  }

  function goHome() {
    navigator("/");
  }

  return (
    <div className="pro_detail memberList">
      <form name="proFrm" className="proFrm">
        <div className="join_base_section">
          <div className="join_base_wrap">
            <div className="filter-header">
              <h1>{proData.name} </h1>
            </div>
            <br />
            <table>
              <tbody>
                <tr>
                  <td className="inputHead"></td>
                  <td>
                    <input
                      type="text"
                      name="name"
                      value={proData.name || ""}
                      disabled
                    />
                  </td>
                </tr>
                <tr>
                  <td className="inputHead"></td>
                  <td>
                    <input type="file" name="upfile" readOnly disabled />
                  </td>
                </tr>
                <tr>
                  <td className="inputHead"></td>
                  <td>
                    <input type="file" name="DTfile" readOnly disabled />
                  </td>
                </tr>
                <tr>
                  <td className="inputHead">  </td>
                  <td className="hasUnit">
                    <input
                      type="number"
                      name="quan"
                      value={proData.quan || ""}
                      readOnly
                      disabled
                    />
                    <span></span>
                  </td>
                </tr>
                {
                  opList.length > 0 && mainCode.length > 0 &&
                     mainCode.map((st, i) => {
                        // key  
                        // console.log(dtList,'++dtList',[i])//  
                        // console.log(mainCode,'++mainCode')/// 
                        // console.log(dtList[i]?.code,'++dtList')//   
                        // console.log(opList,'++opList')//  
                        // console.log(keyList,'++keyList')//   
                        // console.log(dtcode,'++dtcode')//  
                        

                        // mainCode[i]   
                        const matchingOp = opList.find(
                          (op) => op.code == mainCode[i],
                        );
                        // mainCode[i]    
                        const matchingKey = keyList.find(
                          (key) => key.code == mainCode[i],
                        );
                        
                        const opchildCodeArr = dtList.filter(item => item.type === "1").map(item => item.code);
                        const keychildCodeArr = dtList.filter(item => item.type === "0").map(item => item.code);
                        const opchildCode = [...new Set(opchildCodeArr)]
                        const keychildCode = [...new Set(keychildCodeArr)]
                        // console.log(opchildCode);//
                        // console.log(keychildCode);//

                        let max = Math.max(...dtBack.name.map((arr) => arr.length)) - 1;

                        //       
                        let childList01 = [];
                        let childList02 = [];
                        for (let i = 0; i < opchildCode.length; i++){
                          childList01[i] = (dtBack.name[0][opchildCode[i] - 1] || '');
                        }
                        for (let i = 0; i < keychildCode.length; i++){
                          childList02[i] = (dtBack.name[1][keychildCode[i] - 1] || '');
                        }

                        //   opchildCode[i] -  
                        // console.log(dtBack.name[0][opchildCode[i]])
                        // console.log(childList01.length)
                        // console.log(opchildCode.length)
                        console.log(keychildCode)

                        return (
                          <>
                            <tr key={`value-${i}`}>
                              <td className="inputHead">
                                {/*    */}
                                {(matchingOp && matchingOp?.type == 0
                                  ? ""
                                  : "") +
                                  (matchingKey && matchingKey?.type == 1
                                    ? " "
                                    : "") || null}
                              </td>
                              <td>
                                {(matchingOp && matchingOp?.type == 0
                                  ? `${matchingOp.name}`
                                  : "") +
                                  (matchingKey && matchingKey?.type == 1
                                    ? `${matchingKey.name}`
                                    : "") || null}
                              </td>
                            </tr>
                            <tr key={`option-${i}`}>
                              <td className="inputHead">
                                {(matchingOp && matchingOp?.type == 0
                                  ? ""
                                  : "") +
                                  (matchingKey && matchingKey?.type == 1
                                    ? " "
                                    : "") || null}
                              </td>
                              <td>

                                {
                                  (
                                    matchingOp && matchingOp?.type == 0//
                                    ? (childList01) : "")

                                    +

                                    (matchingKey && matchingKey?.type == 1//
                                      ? (childList02) : "")
                                    }
                              </td>
                            </tr>
                          </>
                        );
                      })
                    // : null //,  
                }
                <tr>
                  <td className="inputHead"> </td>
                  {statusChange()}
                </tr>
                <tr>
                  <td className="inputHead"></td>
                  <td>
                    <textarea
                      name="benefit"
                      value={proData.benefit || ""}
                      readOnly
                      disabled
                    />
                  </td>
                </tr>
                <tr>
                  <td className="inputHead"> </td>
                  <td className="hasUnit">
                    <input
                      type="number"
                      name="price"
                      value={orPrice || 0}
                      readOnly
                      disabled
                    />
                    <span></span>
                  </td>
                </tr>
                <tr>
                  <td className="inputHead"></td>
                  <td className="hasUnit">
                    <input
                      type="number"
                      name="discount"
                      value={disV || 0}
                      readOnly
                      disabled
                    />
                    <span>%</span>
                  </td>
                </tr>
                <tr>
                  <td className="inputHead"> </td>
                  <td className="hasUnit">
                    <input
                      type="number"
                      value={fnPrice || 0}
                      readOnly
                      disabled
                    />
                    <span></span>
                  </td>
                </tr>
              </tbody>
            </table>
            <div className="btn-wrap">
              {/* <button type="button" className="write-btn" onClick={goModify}>
                
              </button> */}
              <button type="button" className="write-btn" onClick={goList}>
                
              </button>
              <button type="button" className="home-btn" onClick={goHome}>
                
              </button>
            </div>
          </div>
        </div>
      </form>
    </div>
  );
}

export default ProductsDetailPage;
