import { useState, useEffect } from "react";
import { Link, useParams, useNavigate } from "react-router-dom";
import { detailMem } from "../../services/api";
import { modifyMem } from "../../services/api";
import axios from "axios";
import "./MemberModify.css";

function MemberModify({ id, setShowModify, fetchMembers }) {

    const navigate = useNavigate()
    //const {id} = useParams();
    const [st, setSt] = useState(null)

    useEffect(()=>{
        if(!id){
            return
        }

        detailMem(id)
            .then(res=>{
                setSt(res.data[0])
            })
            .catch(err=>{
                console.log(" ", err)
            })
    },[id])

    if(!st){
        return <div>id </div>
    }

  function stChange(kk, event) {
    setSt({ ...st, [kk]: event.value });
  }

    function submitGo(e){
        e.preventDefault()
        const frmData = new FormData(document.myFrm)
        const myData = Object.fromEntries(frmData)

        modifyMem(myData)
        .then(res=>{
            alert('')
            fetchMembers(); //  
            setShowModify(false); //   
        })
        .catch(err=>{
            console.log(" ",err)
        })
    }

  function back() {
    setShowModify(false); //   
  }

  return (
    <div className="member-modify">
      <h1 className="title">  </h1>
      <form name="myFrm" onSubmit={submitGo} className="modify-form">
        <div className="form-group">
          <label>:</label>
          <input type="text" name="id" value={st.id} readOnly />
        </div>
        <div className="form-group">
          <label>:</label>
          <input
            type="password"
            name="pw"
            defaultValue={st.pw}
            onChange={(e) => stChange("pw", e.target)}
          />
        </div>
        <div className="form-group">
          <label>:</label>
          <input
            type="text"
            name="name"
            defaultValue={st.name}
            onChange={(e) => stChange("name", e.target)}
          />
        </div>
        <div className="form-group">
          <label>:</label>
          <input
            type="text"
            name="nick"
            defaultValue={st.nick}
            onChange={(e) => stChange("nick", e.target)}
          />
        </div>
        <div className="form-group">
          <label>:</label>
          <input
            type="date"
            name="birth_date"
            defaultValue={st.birth_date}
            onChange={(e) => stChange("birth_date", e.target)}
          />
        </div>
        <div className="form-group">
          <label>:</label>
          <input
            type="text"
            name="phone_num"
            defaultValue={st.phone_num}
            onChange={(e) => stChange("phone_num", e.target)}
          />
        </div>
        <div className="form-group">
          <label>:</label>
          <input
            type="email"
            name="email"
            defaultValue={st.email}
            onChange={(e) => stChange("email", e.target)}
          />
        </div>
        <div className="form-group">
          <label>:</label>
          <input
            type="text"
            name="addr"
            defaultValue={st.addr}
            onChange={(e) => stChange("addr", e.target)}
          />
        </div>
        <div className="form-group">
          <label>:</label>
          <select
            name="role"
            value={st.role}
            onChange={(e) => stChange("role", e.target)}
          >
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
          </select>
        </div>
        <div className="form-group">
          <label>:</label>
          <select
            name="type"
            value={st.type}
            onChange={(e) => stChange("type", e.target)}
          >
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
          </select>
        </div>
        <div className="form-group">
          <label>:</label>
          <select
            name="level"
            value={st.level}
            onChange={(e) => stChange("level", e.target)}
          >
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value="VIP">VIP</option>
          </select>
        </div>
        <div className="form-group">
          <label>:</label>
          <input
            type="text"
            name="upt_id"
            defaultValue={st.upt_id}
            onChange={(e) => stChange("upt_id", e.target)}
          />
        </div>
        <div className="form-actions">
          <button type="button" className="cancel-btn" onClick={back}>
            
          </button>
          <button type="submit" className="submit-btn">
            
          </button>
        </div>
      </form>
    </div>
  );
}

export default MemberModify;
